/*body{font-family:Helvetica;}*/
body .proDesc .tabc{padding-bottom: 100px;}
.blockA{
    display: block;
    width: 100%;
    height: 100%;
}
img{
    border:0 none;
    vertical-align: bottom;
}
/*proBaseInfo*/
.proBaseInfo {
    background-color: white;
}
.proBaseInfo .sliderImg{
    position: relative;
    background:url("../imgs/cardetailbj.jpg") no-repeat scroll center 0 / 100% 100%;
    width: 100%;
    min-height: 250px;
}
.proBaseInfo .sliderImg .proImg{
    background-repeat: no-repeat;
    background-position: center center;
    background-size:300px auto;
    min-height:250px;
}
.proBaseInfo .sliderImg .attention
{
    background:url("../imgs/attention.png") no-repeat scroll left center / 18px 18px;
    color: #fff;
    padding-left: 22px;
    position: absolute;
    right: 10px;
    top: 10px;
}
.proBaseInfo .sliderImg .ptx{
    position: absolute;
    bottom: 10px;
    right: 10px;
    color:#fff;
    font-size: 14px;
}
.proBaseInfo ul li{
    padding:15px 10px;
    min-height: 20px;
    line-height: 20px;
    border-bottom: 1px solid #f1f1f1;
    color: #595959;
    font-size: 12px;
}

.proBaseInfo ul li:first-child{
    background: #f7f7f7;
}
.proBaseInfo li h1 {
    color: #262626;
    font-size: 16px;
    line-height: 20px;
    font-weight:normal;
}
.proBaseInfo .proPrice {
    position:relative;
    display:block;
    color:#333;
}
.proBaseInfo .proPrice span{
    display: block;
    margin: 0 0 5px;
}
.proBaseInfo .proPrice .priceoff{
    font-size:16px;
}
.proPrice span:nth-child(2),.proBaseInfo .proID span{
    color:#b2b2b2;
}
.proPrice span:nth-child(2) em{
    text-decoration: line-through;
    color:#333;
}
.proBaseInfo .proPrice .priceoff em{
    background: #ff4b65 url("../imgs/downarrows.png") no-repeat scroll 2px center / 14px 14px;
    border-radius: 3px;
    color: #fff;
    font-size: 14px;
    padding: 3px 5px 3px 18px;
    margin-left: 10px;
}
.proBaseInfo .proPrice .priceoff b{
    color:#e73736;
}
.proBaseInfo .proExt span{
    color:#ff4040;
}
.proBaseInfo .protip .tips{
    color:#ff9845;
}


/*detailList*/
.detailList .arrow ,.detailList .itemNone{
    min-height: 22px;
    line-height: 22px;
    position: relative;
    padding: 15px 30px 15px 10px;
    border-bottom: 1px solid #f1f1f1;
    background: #fff;
}
.detailList .arrow{
    height: 52px;
    line-height: 52px;
    padding:0 30px 0 10px;
}
.detailList .arrow:before{
    right:7px;
}
.detailList .item_arr-right:before{
    opacity: .3;
}
.detailList h1{
    font-weight: normal;
}
.detailList {
    font-size: 14px;
    color: #595959;
    /*border-bottom: 1px solid #f1f1f1;*/
}
.detailList .ico_back:before{
     right: 4px;
     top: 9px;
}
.detailList .chioceC{
    color:#5b5959;
    background: #f7f7f7;
    border-bottom: 1px solid #f1f1f1;
    padding:15px 10px 5px 10px;
    text-align: center;
}
.detailList .chioceC li{
    float: left;
    text-align: center;
    margin-right:5px;
}
.detailList .chioceC li.select span{
    background: #f6d052;
}
.detailList .chioceC span{
    display: inline-block;
    height:20px;
    line-height: 20px;
    border-radius: 6px;
    padding:5px 10px;
    margin:0 0 10px 0;
}
.detailList .chioceC span i{
    margin-right: 8px;
    display: inline-block;
    width:12px;
    height: 12px;
    background: #000;
    vertical-align:-1px;
}

.detailList .chioceC-add li{
    margin-right: 5px;
}
.detailList .chioceC-add span{
    width:auto;
    padding:5px 10px;
}

.detailList .itemNone{
    padding-right:10px;
}
.detailList .disabled{
    padding-right:30px;
}
.detailList .arrow:first-child{
    border-top: 1px solid #f1f1f1;
}

.detailList .arrow h3,.detailList .itemNone h3{
    float: left;
    font-weight: normal;
    width: 56%;
}

.detailList .arrow p,.detailList .itemNone p{
    float: right;
    text-align: right;
}
.detailList .arrow p i {
    width: 12px;
    height: 12px;
    display: inline-block;
    margin-bottom: -1px;
    margin-right: 5px;
}
.detailList .ico1{
    height: 30px;
    right: 3px;
    top: 11px;
    width: 30px;
    position: absolute;

}

.detailList .disabled .ico_arrow-right:before{
    -webkit-transform: rotate(180deg) scale(.7);
    -ms-transform: rotate(180deg) scale(.7);
    -moz-transform: rotate(180deg) scale(.7);
    transform: rotate(180deg) scale(.7);
    opacity:.3;
}
.detailList .last {
    border: 0;
    padding-bottom: 5px;
}
.detailList .arrow .op,.detailList .arrow .ip{
    color:#777;
    font-size: 0;
    height: 23px;
    width: 30%;
}
.detailList .arrow .op *,.detailList .arrow .ip *{
    font-size: 14px;
}

.detailList .chioceC span i em,.detailList .arrow p i em{
    padding: 0;
    margin:0;
    width: 6px;
    height: 12px;
    display: block;
    float: left;
}

.comment{
    background: #fff;
}
.comment-t{
    padding:15px 10px;
    border-bottom: 1px solid #f1f1f1;
    color:#595959;
}
.comment-b{
    padding:0 10px;
}
.comment-b li{
    border-bottom: 1px solid #f1f1f1;
    margin-top: 15px;
}
.comment-b li:last-child{
    border:0 none;
}
.comment-b .cominfo b{
    font-weight: normal;
    color:#ffc822;
}
.comment-b .cominfo span{
    font-size:10px;
    color:#ccc;
}
.comment-b .comtext{
    margin-top: 10px;
    padding-bottom: 15px;
    color:#666;
    font-size: 13px;
}
/*tab*/
.proDesc {
    margin-top: 10px;
    margin-bottom: 65px;
}
.proDesc.headerfixed{
    padding-top:76px;
}

.proDesc .tab {
    background-color: #fff;
    height: 50px;
    line-height: 50px;
    border: 1px solid #f1f1f1;
    padding:0 10px;
}

.proDesc .tab a {
    width:50%;
    float: left;
    display:block;
    text-align: center;
    font-size: 16px;
    margin-top: -3px;
    color:#595959;
}

.proDesc .tab a span {
    display: inline-block;
}

.proDesc .tab a.current span {
    color: rgb(254,152,68);
    border-bottom: 2px solid rgb(254,152,68);
}

.proDesc .tabc {
    min-height: 400px;
    font-size: 14px;
}

.proDesc .attr table {
    background-color: #f9f9f9;
    width: 100%;
    color:#595959;
    font-size: 12px;
}
.proDesc .attr table th,.proDesc .attr table td{
    padding:0 0 0 10px;
}
.proDesc .attr table tr {
    height: 40px;
    border-bottom: 1px solid #f1f1f1;
}

.proDesc .attr table th {
    text-align: left;
    color: #828282;
    background: #f7f7f7;
    font-weight: bold;
    border-left: 3px solid #f8c623;
}

.proDesc .attr table td {
    background: #fff;
}

.proDesc .attr h3 {
    width: 100%;
    float: left;
    color: #494949;
    display: inline-block;
}

/*buy*/
.proAction {
    border-top: 1px solid #f8c623;
    position: fixed;
    bottom: 0;
    background:rgba(255,255,255,0.8);
    padding:5px 0;
    width: 100%;
    background-size:13px 50px;
    z-index: 2;
}

.proAction .btn_addSheme, .proAction .btn_Share {
    float: right;
    margin-right: 6px;
    margin-top: 10px;
}

/*分享层*/
.shareDiv{
    position: fixed;
    bottom:0;
    left:0;
    right: 0;
    background:rgba(255,255,255,1);
    width: 100%;
    z-index: 10000;
    overflow:hidden;
    -webkit-transform: translate3d(0px, 100%, 0px);
    -moz-transform: translate3d(0px, 100%, 0px);
    -ms-transform: translate3d(0px, 100%, 0px);
    transform: translate3d(0px, 100%, 0px);
    -webkit-transition: all ease-out .3s;
    -moz-transition: all ease-out .3s;
    -ms-transition: all ease-out .3s;
    transition: all ease-out .3s;
}
.shareDiv ul{
    padding:20px 0 25px
}
.shareDiv h3 {
    border-bottom: 1px solid #f1f1f1;
    border-top: 5px solid #f8c623;
    color: #000;
    display: block;
    font-size: 24px;
    font-weight: normal;
    height: 60px;
    line-height: 60px;
    margin: 0;
    padding: 0;
    text-align: center;
}
.shareDivUp{
    -webkit-transform: translate3d(0px, 0%, 0px);
    -moz-transform: translate3d(0px, 0%, 0px);
    -ms-transform: translate3d(0px, 0%, 0px);
    transform: translate3d(0px, 0%, 0px);
}
.bdshare-button-style0-16{
    padding:0 !important;
}
.bdshare-button-style0-16 a.bds_tsina,.bdshare-button-style0-16 a.bds_tqq,.bdshare-button-style0-16 a.bds_weixin{
    text-align: center;
    padding: 70px 0 0;
    margin:0;
    color:#525252;
}
.bdshare-button-style0-16 a.bds_tsina{
    background:url("../imgs/sinablog.png") no-repeat center top / 60px auto;
}
.bdshare-button-style0-16 a.bds_tqq{
    background:url("../imgs/tencent.png") no-repeat center top / 60px auto;
}
.bdshare-button-style0-16 a.bds_weixin{
    background:url("../imgs/wechat.png") no-repeat center top / 60px auto;
}
#shareOver{
    position: fixed;
    top:5px;
    left: 5%;
    z-index: 9999999;
    width: 90%;
}


