/*new icon S*/
.ico1, .ico2{
	position: relative;
	display: inline-block;
	overflow: hidden;
	vertical-align: middle;
}
.ico1:before,
.ico2:before,
.item_arr-right:before,
.item_arr-up:before,
.item_arr-down:before{
	content: "";
	position: absolute;
	top: 50%;
}
.ico1:before,
.ico2:before{
	left: 50%;
}
.ico1:before,
.item_arr-right:before,
.item_arr-up:before,
.item_arr-down:before{
	background: url(../imgs/ico_sprite.png) no-repeat;
	background-size: 96px 177px;
}
.ico1:before{
    width: 23px;
	height: 23px;
    margin-left: -11px;
    margin-top: -11px;
}
.ico2:before{
	width:40px;
	height:40px;
	margin-left: -20px;
	margin-top: -20px;
	background:url(../imgs/index_sp.png) no-repeat;
	background-size: 192px 61px;
}
.ico_arrow-left:before,
.ico_arrow-right:before,
.ico_arrow-up:before,
.ico_arrow-down:before,
.item_arr-right:before,
.item_arr-up:before,
.item_arr-down:before{
    -webkit-transition: -webkit-transform .3s ease;
    -ms-transition: -ms-transform .3s ease;
    -moz-transition: -moz-transform .3s ease;
    transition: transform .3s ease;
}
.ico_arrow-left:before,
.ico_arrow-right:before,
.ico_arrow-up:before{
    background-position: -3px -5px;
}
.ico_arrow-up:before{
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    transform: rotate(90deg);
}
.ico_arrow-right:before,
.item_arr-up:before{
    -webkit-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
    transform: rotate(-180deg);
}
.ico_arrow-down:before,
.item_arr-right:before{
    -webkit-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
    transform: rotate(-90deg);
}
.ico_edit:before{
    background-position: -34px -4px;
}
.ico_share:before {
    background-position: -3px -30px;
}
.ico_share:active:before {
    background-position: -37px -30px;
}
.ico_log_wx:before{
	width: 33px;
	height: 28px;
	margin-left: -16px;
	margin-top: -14px;
	background-position: 0 -145px;
}
.ico_menu:before{
	background-position:-124px 8px;
}
.ico_reg:before{
	background-position:-154px 8px;
}
