/*CSS icon*/
/*icss1返回icon, icss2展开更多icon*/
.icss1, .icss2{
	position: relative;
	display: inline-block;
    width: 50px;
    height: 50px;
    text-indent: -10000px;
    text-align: left;
    overflow: hidden;
}
.icss1:after,
.icss2:after {
    position: absolute;
    top: 50%;
    content: '';
    width: 16px;
    height: 16px;
    margin-top: -8px;
    border-bottom: 1px solid #888;
}
.icss1:after {
    left: 20px;
    border-left: 1px solid #888;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    transform: rotate(45deg);
}
.icss2:after {
    right: 20px;
    border-right: 1px solid #888;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

