	width:20px;
	height: 20px;
	right: 18px;
    margin-top: -10px;
    background-position: 0 -94px;
}
.item .item_left,
.item .item_right{
	float: left;
	font-size: 16px;
	line-height: 44px;
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
	box-sizing:border-box;
}
.item .item_left{
	width: 40%;
	height: 44px;
	white-space: nowrap;
}
.item .item_right{
	width: 60%;
	height: 44px;
	padding-right: 20px;
	text-align: right;
}
.item_multi{
	display: table;
	height: auto;
	padding-top: 10px;
	padding-bottom: 10px;
	vertical-align: middle;
	table-layout: fixed;
}
.item_multi .item_left,
.item_multi .item_right{
	float: none;
	display: table-cell;
	height: auto;
	line-height: 1.5;
	vertical-align: middle;
}
/*ui component*/
.markdiv {
	background: #000;
	opacity: .5;
	position:fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
}

.alertdiv {
	position: fixed;
	left: -100%;
	right: 100%;
	top: 0;
	bottom: 0;
	background-color: #ccc;
	text-align: center;
	font-size: 0;
	z-index:10001;
}
.alertdiv:after {
	content: "";
	display: inline-block;
	vertical-align: middle;
	height: 100%;
	width: 0;
}
.alertdiv .msg {
	display: inline-block;
	vertical-align: middle;
	text-align: left;
	position: relative;
	right: -100%;
	background:#fff;
	color: #000;
	text-align: center;
	font-size:14px;
	padding: 10px;
	border-radius: 3px;
	max-width:250px;
}

.alertdiv .confirm {
	display: inline-block;
	vertical-align: middle;
	text-align: left;
	position: relative;
	right: -100%;
	background: #fff;
	color: #666;
	font-size:12px;
	padding: 0;
	width:80%;
}

.alertdiv .confirm h3{
	height:50px;
	background:#ececec;
	color:#282828;
	padding-left:20px;
	line-height:50px;
	font-size:14px;
	text-align:left;
}
.alertdiv .confirm .confirmp{
	padding:10px;
}
.alertdiv .confirm .confirmbtnp{
	display:-webkit-box;
	display:-moz-box;
	display:-webkit-flex;
	display:flex;

	-moz-box-sizing:border-box;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	padding:10px;
}

.alertdiv .confirm .confirmbtnp .btna{
	display:block;
	-webkit-box-flex:1;
	-moz-box-flex:1;
	-webkit-flex:1;
	flex:1;
	height:36px;
	line-height:36px;
	text-align:center;
	margin:0 10px;
	padding:0 5px;
	border-radius:3px;
	background:#f7c53a;
	color:#262626;
	font-size:14px;
	border:1px solid #f7c53a;
}

.alertdiv .confirm .confirmbtnp .btna:first-child{
	background: #fafafa;
	border:1px solid #f7c53a;
}

/*popup*/
.car-popup {
    display: block;
    padding: 0;
    opacity: 1;
    position: fixed;
    z-index: 10005;
    top: 50%;
    left: 50%;
    margin: 0 auto;
    background: #fff;
    color:#555;
    box-shadow:1px 1px 1px #777;
    -webkit-box-shadow:1px 1px 1px #777;
    -moz-box-shadow:1px 1px 1px #777;
    box-shadow:1px 1px 1px #777;
}

.car-popup.hidden {
    opacity: 0;
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    transform: scale(0);
    top: 50%;
    left: 50%;
    margin: 0 auto;
}

.car-popup h2{
    font-size:16px;
    margin:0;
    padding:0;
    background: #eee;
    color: #888;
    height: 40px;
    text-align:center;
    line-height: 40px;
    clear:both;
    display:block;
    position: static;
}

.car-popup .car-popup-cancel,
.car-popup .car-popup-ok{
    float:left;
    width: 50%;
    background: #eaeaea;
    color:#555;
    height: 30px;
    line-height: 30px;
}

.car-popup .content-body{
	position: relative;
	z-index: 1001;
	width: 100%;
	height: 100%;
}
.car-popup .center{
    float:none;
    width:100%;
    margin:auto;
    display: block;
}

.uiload {
	position:fixed;
	z-index: 10001;
	left: -100%;
	right: 100%;
	top: 0;
	bottom: 0;
	background-color: #CCC;
	text-align: center;
	font-size: 0;
	z-index:10001;
}
.uiload:after {
	content: "";
	display: inline-block;
	vertical-align: middle;
	height: 100%;
	width: 0;
}
.uiload .uiloadicon {
	width: 100px;
	height: 33px;
	line-height: 32px;
	display: inline-block;
	vertical-align: middle;
	text-align: left;
	position: relative;
	right: -100%;
	color:#ffde00;
	background: rgba(0, 0, 0, 0.7);
	border-radius: 3px;
	padding: 0 10px;
	font-size: 14px;

}
.ui-loading {
    display: inline-block;
    margin: 0 5px 0 0;
    vertical-align: -1px;
}
.ui-loading i {
    background:#ffde00;
    display: inline-block;
    height: 12px;
    width: 5px;
    margin-right: 5px;
}
.ui-loading i {
    -webkit-animation:loading-spin 1s linear 0s infinite ;
    -moz-animation:loading-spin 1s linear 0s infinite ;
    -ms-animation:loading-spin 1s linear 0s infinite ;
    animation:loading-spin 1s linear 0s infinite ;
}
.ui-loading i.t2 {
   -webkit-animation-name: loading-spin-one;
   -ms-animation-name: loading-spin-one;
   -moz-animation-name: loading-spin-one;
   animation-name: loading-spin-one;

}
.ui-loading i.t3 {
    -webkit-animation-name: loading-spin-two;
    -ms-animation-name: loading-spin-two;
    -moz-animation-name: loading-spin-two;
    animation-name: loading-spin-two;
}
/*loading-spin*/
@-webkit-keyframes loading-spin {
    0% {opacity: 0; }
    30% { opacity: 1;-webkit-transform: scale(1, 1.2);}
    60% { opacity: 0;-webkit-transform: scale(1);}
    100% {opacity: 0;}
}
@-moz-keyframes loading-spin {
    0% {opacity: 0; }
    30% { opacity: 1;-moz-transform: scale(1, 1.2);}
    60% { opacity: 0;-moz-transform: scale(1);}
    100% {opacity: 0;}
}
@-ms-keyframes loading-spin {
    0% {opacity: 0; }
    30% { opacity: 1;-ms-transform: scale(1, 1.2);}
    60% { opacity: 0;-ms-transform: scale(1);}
    100% {opacity: 0;}
}
@keyframes loading-spin {
    0% {opacity: 0; }
    30% { opacity: 1;transform: scale(1, 1.2);}
    60% { opacity: 0;transform: scale(1);}
    100% {opacity: 0;}
}
/*loading-spin-one*/
@-webkit-keyframes loading-spin-one {
    0% { opacity: 0;}
    20% {opacity: 0;}
    50% {opacity: 1;-webkit-transform: scale(1, 1.2);}
    80% {opacity: 0;-webkit-transform: scale(1); }
