/*checkout*/
.none{
    display: none;
}
.checkout {
    overflow: hidden;
    color: #595959;
}
.checkout .car {
    color: #595959;
    background-color: white;
    min-height: 140px;
    position: relative;
    border-top: 1px solid #f1f1f1;
}

.checkout .car .carImg {
    width: 55%;
    min-height: 140px;
    float: left;
    background-position: 0 center;
    background-repeat: no-repeat;
    background-size: 100% auto;
}

@media screen and (min-width:500px){
    .checkout .car .carImg {
        background-size: 280px auto;
    }
}
.checkout .car .name {
    padding: 20px 10px 10px 0;
    float: left;
    width: 45%;
}

.checkout .car .name p {
    font-size: 12px;
}
.checkout .car .name p em{
    color:#6f6f6f;
    display: block;
    margin-bottom: 5px;
}
.checkout .car .name p i{
    font-size: 10px;
    font-style: normal;
}

.checkout .car .name h2 {
    font-size: 16px;
    font-weight: normal;
}

.checkout .scheme li.title {
    margin: 0;
    padding:10px 0 10px 15px;
    font-size: 12px;
    border-top:1px solid #f1f1f1;
    border-bottom:1px solid #f1f1f1;
    background: #fff;
    color:#fe9844;
}
.checkout .scheme li.question{
   position: relative;
}
