.checkout .scheme {
    background-color: #fff;
    font-size:14px;
    border-bottom: 1px solid #f1f1f1;
}

.checkout .scheme li {
    border-bottom: 1px solid #f1f1f1;
    margin:0 18px;
    font-size: 14px;
    position: relative;
    padding:15px 0 15px 0;
}
.checkout .scheme li.item_arr-down {
    padding-right: 25px;
}
.checkout .scheme li:last-child {
    border-bottom:none;
}



.checkout .scheme li.title a {
    /*background: url(../imgs/question.png) left 6px no-repeat;*/
    width: 30px;
    height: 31px;
    display: inline-block;
    background-size: 30px 31px;
    cursor: pointer;
}

.checkout .scheme li.title a:hover {
    /*background: url(../imgs/question_hover.png) left 6px no-repeat;*/
    background-size: 30px 31px;
}

.checkout .scheme li.title .tip {
    position: absolute;
    width: 300px;
    height: auto;
    border: 1px solid #d4d4d4;
    border-radius: 3px;
    padding: 20px;
    background-color: white;
    z-index: 4;
    left: 140px;
    top: 5px;
    display: none;
}

.checkout .scheme li.openT {
    margin:-1px 0 0;
    background: #f0f0f0;
    padding:0;
}

.checkout .scheme li span {
    float: right;
}
.checkout .scheme li.item_arr-down:before{
    right:0;
}

.checkout .scheme li .ex_oper {
     color:#595959;
     padding: 10px 18px;
}
.checkout .scheme li .ex_oper dt{
  font-size:12px;
  padding:10px 25px 10px 0;
  border-bottom: 1px solid #ccc;

}
.checkout .scheme li .ex_oper dt.lastL{
    margin-top:-1px;
    border-bottom:none;
}
.checkout .scheme li .ex_oper dt span{
     right:38px;
}
.checkout .scheme li .ex_oper dl dt{
     border-top: 1px solid #e1e1e1;
     border-bottom: 1px solid #e1e1e1;
}

.checkout .scheme li .ex_oper dl:first-child dt{
     border-top: none;
}
.checkout .scheme li .ex_oper dd{
     line-height:22px;
     padding:15px 0 15px 10px;
     font-size:14px;
}


