.checkout .price {
    font-size: 16px;
    text-align: right;
    height:40px;
    color:#595959;
    line-height: 40px;
    border-bottom: 1px solid #f1f1f1;
    padding: 10px 20px 10px 0;
}

.checkout .price span {
    color: #e60012;
    font-size: 20px;
}

.checkout .agree {
     color:#595959;
    line-height: 40px;
    height:40px;
    background-color: white;
    padding: 10px 0 10px 18px;
    border-bottom: 1px solid #f1f1f1;
    font-size: 14px;
}

.checkout .agree span a{
    text-decoration: underline;
}

.checkout .checkbox_wrap {
    display: inline-block;
    width: 18px;
    height: 18px;
    border: 1px solid #cbcbcb;
    border-radius: 50% 50%;
    vertical-align: -5px;
    margin-right: 5px;
}

.checkout .userinfo {
    margin:18px;
    background-color: white;
    -moz-border-radius: 5px;
    /* Gecko browsers */

    -webkit-border-radius: 5px;
    /* Webkit browsers */

    border-radius: 5px;
    border: 1px solid #f1f1f1;
}

.checkout .userinfo li {
    line-height: 50px;
    padding: 0px 0px 0px 20px;
    border-bottom: 1px solid #f1f1f1;
    font-size: 14px;
    position: relative;
    color:#595959;
}

.checkout .userinfo li.last {
    border: 0;
}

.checkout .userinfo input{
     color:#b3b3b3;
}
.checkout .userinfo li a {
    color:#eb6100;
    border-left: 1px solid #f1f1f1;
    display: inline-block;
    line-height: 50px;
    position: absolute;
    right: 20px;
    padding-left: 10px;
}

.checkout .chosePayTypeTit {
    color:#6f6f6f;
    font-size: 14px;
    padding:0 18px 10px 18px;
    position: relative;
    height: 40px;
    line-height: 40px;
    font-weight: normal;
}

.checkout .chosePayTypeTit span {
    position: absolute;
    right: 18px;
    font-size: 16px;
}

.checkout .chosePayTypeTit span i,
.checkout .chosePayTypeTit span em {
    color: #e60012;
    font-size: 20px;
}
.checkout .txt-tip{
    padding: 10px 18px;
    font-size: 12px;
    line-height: 20px;
    background-color: #fff;
    color: #999;
}
.checkout .chosePayType {
    background-color: white;
    text-align: center;
    border-top: 1px solid #d4d4d4;
    padding:15px 18px;
}

.checkout .chosePayType a {
    width: 48%;
    height: 50px;
    border: 1px solid #f1f1f1;
    border-radius: 5px;
    display: inline-block;
}

.checkout .chosePayType a.btnAlipay {
    background: url("../imgs/payZFB.jpg") no-repeat center center;
    background-size: contain;
}

.checkout .chosePayType a.btnWeixinpay {
    background: url("../imgs/payWX.jpg") no-repeat center center;
    background-size: contain;
}

.checkout .errMsg {
    background: #fd7d7c;
    line-height: 40px;
    padding: 10px 20px 10px 20px;
    color: white;
}

.checkout .checkbox_checked {
    background: #f6d052;
    border-color: #f6d052;
    position: relative;
}

.checkbox_only_checked {
    background: none;
    border: none;
    position: relative;
}

.checkbox_only_checked:before,
.checkbox_checked:before {
    content: "";
    width: 5px;
    height: 10px;
    left:6px;
    top:2px;
    display: block;
    position: absolute;
    border-bottom: 1px solid #000;
    border-right: 1px solid #000;
     -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
}

.checkbox_box {
    border-radius: 0;
}
.favorable {
    margin: 16px auto 0;
    width: 94%;
}
.favorable #p1,.favorable #p2{
    float: left;
}
.favorable #p1 {
    margin-right:2%;
    width: 73%;
}
.favorable #p1 .menu1 {
    background:#fff;
    color: #666;
    border: 1px solid #d4d4d4;
    border-radius: 5px;
    font-size: 14px;
    height: 36px;
    line-height: 36px;
    padding-left: 3%;
    width: 96%;
}

.favorable #p2 {
    text-align: left;
    width: 25%;
}
.favorable #p2 .menu2 {
    background: #ffd800;
    border: 1px solid #ffd800;
    border-radius: 5px;
    color: #555;
    font-size: 14px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    width: 100%;
}


