/*加减控件*/
.ui_adjust {
    height: 18px;
    color: #919191;
    background-color: #fff;
    display: inline-block;
    vertical-align: middle;
}

.ui_adjust .ui_adjust_minus,
.ui_adjust .ui_adjust_plus,
.ui_adjust .ui_adjust_view {
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    float: left;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.ui_adjust .ui_adjust_minus,
.ui_adjust .ui_adjust_plus {
    cursor: pointer;
    border: 1px solid #d8d8d8;
    background: #ededed;
}

.ui_adjust .ui_adjust_minus.disabled,
.ui_adjust .ui_adjust_plus.disabled {
    cursor: default;
    background-color: #fff;
    border-color: #d8d8d8;
}

.ui_adjust .ui_adjust_minus.disabled::before,
.ui_adjust .ui_adjust_minus.disabled::after,
.ui_adjust .ui_adjust_plus.disabled::before,
.ui_adjust .ui_adjust_plus.disabled::after {
    background-color: #d8d8d8;
}

.ui_adjust .ui_adjust_minus:active,
.ui_adjust .ui_adjust_plus:active {
    background-color: #d8d8d8;
}

.ui_adjust .ui_adjust_minus:active::before,
.ui_adjust .ui_adjust_minus:active::after,
.ui_adjust .ui_adjust_plus:active::before,
.ui_adjust .ui_adjust_plus:active::after {
    background-color: #fff;
}

.ui_adjust .ui_adjust_minus {
    border-right: none;
    position: relative;
}

.ui_adjust .ui_adjust_minus::before {
    content: "";
    height: 2px;
    width: 10px;
    background-color: #000;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}

.ui_adjust .ui_adjust_minus + .ui_adjust_plus {
    border-left: 1px solid #d8d8d8;
}

.ui_adjust .ui_adjust_plus {
    position: relative;
    border-left: none;
}

.ui_adjust .ui_adjust_plus::before,
.ui_adjust .ui_adjust_plus::after {
    content: "";
    width: 10px;
    height: 2px;
    background-color: #000;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
}

.ui_adjust .ui_adjust_plus::after {
    width: 2px;
    height: 10px;
}

.ui_adjust .ui_adjust_view {
    width: 60px;
    border: 1px solid #d8d8d8;
    overflow: hidden;
}
/*ios on-off control*/
.switch_box {
    position: relative;
    width: 72px;
    height: 28px;
    display: inline-block;
    vertical-align: middle;
    border-radius: 14px;
    background-color: #ccc;
    cursor: pointer;
    -webkit-transition: 200ms background-color ease-in;
    -moz-transition: 200ms background-color ease-in;
    transition: 200ms background-color ease-in;
}

.switch_box.switch_box-on {
    background-color: #37d28e;
}
.switch_box:before,
.switch_box.switch_box-on:before{
    z-index: 1;
    position: absolute;
    top: 0;
    line-height: 28px;
    font-size: 14px;
    color: #fff;
}
.switch_box:before{
    content: "否";
    right: 27px;
    text-indent: 0;
}
.switch_box-on1:before{
    content: "是";
    right: 30px;
    text-indent: 0;
}
.switch_box.switch_box-on:before {
    content: "是";
    left: 27px;
    text-align: left;
}
.switch_box .switch_scroll {
    position: absolute;
    top: 2px;
    left: 2px;
    width: 24px;
    height: 24px;
    border-radius: 12px;
    background-color: #fff;
    -webkit-transition: 200ms left ease-in;
    -ms-transition: 200ms left ease-in;
    -moz-transition: 200ms left ease-in;
    transition: 200ms left ease-in;
}
.switch_box-on1 .switch_scroll,
.switch_box.switch_box-on .switch_scroll {
    left: 46px;
}
.switch_box-txt{
    line-height: 28px;
    text-indent: 80px;
    color: #b2b2b2;
}
.switch_box-txt.switch_box-on,
.switch_box-txt.switch_box-on1{
    color: #595959;
}

