.iclick{
	position: relative;
}
.iclick:active{
	background-color: #9B0117;
}
.i3, .iclick:before, .itime{
	display: inline-block;
	overflow: hidden;
	background-image: url(../imgs/ico_index.png);
	background-repeat: no-repeat;
	background-size:  320px 244px;
}
.iclick:before{
	content: "";
	position: absolute;
	top:50%;
	left: 50%;
}
.iclick1:before,
.iclick2:before{
	width: 20px;
	height: 16px;
	margin-top: -10px;
	margin-left: -8px;
}
.iclick3:before{
	width: 10px;
	height: 10px;
	margin-top: -5px;
	margin-left: -5px;
}
.iclick1:before{
	background-position: -228px -120px;
}
.iclick2:before{
	background-position: -249px -120px;
}
.iclick3:before{
