.car_logo_outer_wrap{
	position: relative;
	min-height: 120px;
	margin: 10px 0;
	overflow: hidden;
	-webkit-transition: all 0.3s ease-out;
	-ms-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
}

.car_logo_wrap{
	-webkit-transition: all 0.3s ease-out;
	-ms-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
}
.car_logo_fix{
	position: fixed;
	top: 50px;
	z-index: 1000;
	width:100%;
	left: 0;
}

.car_logo{
	float: left;
	position: relative;
	width: 20%;
	height: 50px;
	line-height: 50px;
	border-top: 1px solid #f1f1f1;
	background-color: #fff;
	text-align: center;
	overflow: hidden;
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
	box-sizing:border-box;
}
.car_logo .car_img{}
.car_logo img{
	width: 55px;
	vertical-align: -13px;
}
.car_logo:not(:nth-child(5n+1)){
	border-left: 1px solid #f1f1f1;
}

