/* 后半部分公用代码 */
.buy-note,
.we-promise,
.btm-footer {
	width: 100%;
	color: #777;
	background-color: #fff;
}
.btm-hd {
	position: relative;
	height: 40px;
	font-size: 16px;
	text-indent: .5em;
	line-height: 40px;
	color: #959492;
	background-color: #fff;
}
.btm-hd li a{
	display: block;
	width: 100%;
	height: 100%;
}
.btm-hd:before {
	content: "";
	display: block;
	float: left;
	width: 3px;
	height: 40px;
	background-color: #f1324e;
}
.btm-hd .btm-hd-more{
	position: absolute;
	height: 40px;
	top:0;
	right: 7px;
	color: #666;
	font-size: 14px;
}
.btm-hd .countdown{
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	color: #888;
	font-size: 12px;
	text-align: center;
}
.countdown .itime{
	display: inline-block;
	height: 16px;
	margin: 0 3px;
	padding: 0 2px;
	line-height: 16px;
	color: #fff;
	text-indent: 0;
	text-align: center;
	vertical-align: -4px;
}
.countdown .itime-day{
	width: 6px;
}
.countdown .itime-hour,
.countdown .itime-min,
.countdown .itime-sec{
	width: 17px;
	letter-spacing: 4px;
}
.itime-day,.itime-hour, .itime-min{
	background-position: -280px -136px
}
.itime-sec{
	background-position: -280px -120px;
}
