/*lft-move */
@-webkit-keyframes lft-move {
    0% {
        -webkit-transform: translate3d(-122px, 0, 0);
    }
    100% {
        -webkit-transform: translate3d(0, 0, 0);
    }
}
@-moz-keyframes lft-move {
    0% {
        -moz-transform: translate3d(-122px, 0, 0);
    }
    100% {
        -moz-transform: translate3d(0, 0, 0);
    }
}
@-ms-keyframes lft-move {
    0% {
        -ms-transform: translate3d(-122px, 0, 0);
    }
    100% {
        -ms-transform: translate3d(0, 0, 0);
    }
}
@keyframes lft-move {
    0% {
        transform: translate3d(-122px, 0, 0);
    }
    100% {
        transform: translate3d(0, 0, 0);
    }
}

/*rlt-move*/
@-webkit-keyframes rlt-move {
    0% {
        -webkit-transform: translate3d(0, 0, 0);
    }
    100% {
        -webkit-transform: translate3d(-122px, 0, 0);
    }
}
@-moz-keyframes rlt-move {
    0% {
        -moz-transform: translate3d(0, 0, 0);
    }
    100% {
        -moz-transform: translate3d(-122px, 0, 0);
    }
}
@-ms-keyframes rlt-move {
    0% {
        -ms-transform: translate3d(0, 0, 0);
    }
    100% {
        -ms-transform: translate3d(-122px, 0, 0);
    }
}
@keyframes rlt-move {
    0% {
        transform: translate3d(0, 0, 0);
    }
    100% {
        transform: translate3d(-122px, 0, 0);
    }
}

